body {
  background-color: #f8f8f8;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

[class$=__heading] {
  text-align: center;
  margin-bottom: 5rem;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 8rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  background-image: url(../img/header_bg.svg);
  background-repeat: no-repeat;
  background-position: top right;
  background-size: 10rem;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .header {
    background-size: 50rem;
  }
}
@media (min-width: 992px) {
  .header {
    background-size: 60rem;
  }
}
@media (min-width: 1024px) {
  .header {
    background-size: 110rem;
  }
}
@media (min-width: 768px) {
  .header__barra {
    display: flex;
    justify-content: space-between;
  }
}
.header__logo {
  width: 15rem;
  margin: 0 auto 3rem auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
  align-items: center;
}
.header__heading {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
    font-size: 5rem;
    line-height: 1.2;
  }
}
@media (min-width: 768px) {
  .header__imagen {
    max-width: 30rem;
    margin: 0 auto;
  }
}
.header___boton {
  background-color: #6834a6;
  display: block;
  padding: 1rem;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .header___boton {
    padding: 1rem 3rem;
    display: inline-block;
  }
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    align-items: flex-start;
    gap: 2rem;
  }
}
.navegacion__link {
  color: #6834a6;
}
@media (min-width: 768px) {
  .navegacion__link {
    color: #FFFFFF;
  }
}
.navegacion__link--blanco {
  color: #FFFFFF;
}

.footer {
  background-color: #5A30a0;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .footer__grid {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.footer__logo {
  width: 20rem;
  margin: 0 auto 4rem auto;
}
@media (min-width: 768px) {
  .footer__logo {
    margin: 0;
  }
}

.nucleos {
  margin-top: 3rem;
}
.nucleos__grid {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .nucleos__grid {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 5rem;
    align-items: center;
  }
}

.listado__elemento {
  background-color: #FFFFFF;
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.15);
  padding: 2rem;
  margin-bottom: 5rem;
  transition-property: transform;
  transition-duration: 300ms;
}
.listado__elemento:hover {
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .listado__elemento--2col {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
}
.listado__heading {
  font-size: 3rem;
  color: #6834a6;
  margin: 0;
}
.listado__numero {
  font-size: 6rem;
  margin: 0;
  font-weight: 900;
  color: #6834a6;
  text-align: center;
}
@media (min-width: 768px) {
  .listado__numero {
    flex-basis: 20rem;
    text-align: right;
    font-size: 4rem;
  }
}
.listado_texto {
  margin: 0;
  font-size: 2rem;
}

.seguridad {
  background-color: #6834a6;
  padding: 20rem 0;
  position: relative;
  margin: 10rem 0;
  overflow: hidden;
}
.seguridad::before, .seguridad::after {
  background-color: #FFFFFF;
  content: "";
  height: 20rem;
  width: 120%;
  position: absolute;
}
.seguridad::before {
  top: -10rem;
  left: 0;
  transform: rotate(3deg);
}
.seguridad::after {
  bottom: -10rem;
  left: 0;
  transform: rotate(-3deg);
}
.seguridad__heading {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .seguridad__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .comisiones__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
    align-items: flex-end;
  }
}
@media (min-width: 992px) {
  .comisiones__grid {
    align-items: center;
  }
}
.comisiones__texto {
  text-align: center;
  font-size: 2.4rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .comisiones__texto {
    text-align: left;
  }
}

.testimoniales {
  background-color: #6834a6;
  padding: 5rem 0;
}
.testimoniales__heading {
  color: #FFFFFF;
}
.testimoniales__grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
}
@media (min-width: 768px) {
  .testimoniales__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.testimonial {
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.15);
  background-color: #FFFFFF;
  padding: 2rem;
  position: relative;
}
.testimonial__texto {
  padding-left: 4rem;
}
.testimonial__texto::before {
  content: "";
  background-image: url(../img/comilla.png);
  background-size: 3rem;
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  height: 3rem;
  position: absolute;
  top: 3.5rem;
  left: 2rem;
}
.testimonial__autor {
  color: #6834a6;
  font-weight: 700;
  text-align: right;
}